import { useContext } from 'react';
import { Box, Container, useMediaQuery, useTheme } from '@mui/material';
import { SettingsContext } from '../../context/SettingsContext';
import CustomAppBar from '../../components/shared-components/AppBar/AppBar';
import ProfileCard from "../../components/shared-components/ProfileCard";
import Footer from '../../components/shared-components/Footer';
import ChatbotCard from '../../components/chatbot-components/ChatbotCard';
import MarkdownViewer from "../../components/shared-components/MarkdownViewer";

import { motion } from 'framer-motion';

const fadeInVariants = {
  hidden: { opacity: 0, y: 20 },
  visible: { opacity: 1, y: 0, transition: { duration: 0.5 } },
};

const Home = () => {
  const { language, themeMode } = useContext(SettingsContext);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));

  const intro_filePath = language === "en" ? "md/en/steven_en.md" : "md/cn/steven_cn.md";
  const about_filePath = language === "en" ? "md/en/about_website_en.md" : "md/cn/about_website_cn.md";

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
      }}
    >
      <CustomAppBar />

      <Box
        sx={{
          marginTop: isMobile ? '120px' : '64px',
          display: 'flex',
          flexDirection: 'column',
        }}
      >

        <Container
          maxWidth={isMobile ? 'sm' : 'lg'}
          sx={{
            display: 'flex',
            flexDirection: isMobile ? 'column' : 'row',
            alignItems: 'center',
            justifyContent: 'center',
            minHeight: isMobile? "auto" : 'calc(100vh - 64px)',
          }}
        >

          <motion.div initial="hidden" animate="visible" variants={fadeInVariants}>
          <Box
            sx={{
              flex: isMobile ? 'none' : '1 1 20%',
              width: isMobile ? '100%' : 'auto',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
            }}
          >
            <ProfileCard />
          </Box>
          </motion.div>

          <motion.div initial="hidden" animate="visible" variants={fadeInVariants} style={{ width: '100%', height: '100%' }}>
            <Box
              sx={{
                flex: isMobile ? 'none' : '1 1 80%',
                width: isMobile ? '100%' : 'auto',
                display: 'flex',
                overflow: 'auto',
                alignItems: 'center',
                justifyContent: 'center',
              }}
            >
              <MarkdownViewer filePath={intro_filePath} />
            </Box>
          </motion.div>

        </Container>


        <Container
          maxWidth="lg"
          sx={{
            minHeight: '1px',
            borderTop: isMobile
              ? 'none'
              : `1px solid ${themeMode === 'light' ? '#ccc' : '#555'}`,
          }}
        />

        <Container
          sx={{
            minHeight: '100vh',
            display: 'flex',
            flexDirection: isMobile ? 'column' : 'row', // 动态设置 flexDirection
            maxWidth: isMobile ? 'sm' : 'lg',
          }}
        >
          <Box
            sx={{
              flex: isMobile ? 'none' : '1 1 65%', // 左侧宽度为 65%
              width: isMobile ? '100%' : 'auto',
              display: 'flex',
              overflow: 'auto',
              alignItems: 'center',
              justifyContent: 'center',
            }}
          >
            <MarkdownViewer filePath={about_filePath} />
          </Box>

          <Box
            sx={{
              flex: isMobile ? 'none' : '1 1 35%',
              width: isMobile ? '100%' : 'auto', // 在移动端占满宽度
              display: 'flex', // 始终显示
              alignItems: 'center',
              marginTop: isMobile ? 2 : 0, // 在移动端增加顶部间距
              marginBottom: isMobile ? 2: 0,
              paddingLeft: isMobile ? 0 : 4, // 在移动端移除左侧内边距
            }}
          >
            <ChatbotCard />
          </Box>
        </Container>
      </Box>

      {/* 下部 Footer */}
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          backgroundColor: 'background.default',
          borderTop: (theme) => ({
            xs: 'none', // 在 xs 屏幕无边框
            md: `1px solid ${theme.palette.divider}`, // 在 md 屏幕及以上显示边框
          }),
          maxWidth: (theme) => theme.breakpoints.values.lg, // 限制最大宽度为 lg
          width: '100%', // 确保宽度是 100% 的父元素宽度
          mx: 'auto', // 水平居中
        }}
      >
        <Footer />
      </Box>

    </Box>
  );
};

export default Home;