import { useState, useRef, useEffect, useContext } from 'react';
import { SettingsContext } from '../../context/SettingsContext';
import { 
  Box, 
  Typography, 
  Paper, 
  TextField, 
  IconButton, 
  Container,
  CircularProgress,
  Avatar
} from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import BotAvatar from '@mui/icons-material/SmartToy';
import { motion, AnimatePresence } from 'framer-motion';
import ChatbotService from '../../services/ChatbotService';
import { useUser } from "../../context/UserContext";

const TypewriterText = ({ text, onComplete }) => {
  const [displayedText, setDisplayedText] = useState('');
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isComplete, setIsComplete] = useState(false);

  useEffect(() => {
    if (currentIndex < text.length) {
      const timer = setTimeout(() => {
        setDisplayedText(prev => prev + text[currentIndex]);
        setCurrentIndex(currentIndex + 1);
      }, 30);
      
      return () => clearTimeout(timer);
    } else {
      setIsComplete(true);
      if (onComplete) {
        onComplete();
      }
    }
  }, [currentIndex, text, onComplete]);

  return (
    <>
      {displayedText}
      {!isComplete && (
        <motion.span
          animate={{ opacity: [0, 1, 0] }}
          transition={{ repeat: Infinity, duration: 0.8 }}
        >
          |
        </motion.span>
      )}
    </>
  );
};

// 快捷问题链接组件
const QuickQuestionLink = ({ question, onQuestionClick, delay = 0, themeMode }) => {
  const [showQuestion, setShowQuestion] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => {
      setShowQuestion(true);
    }, delay);
    
    return () => clearTimeout(timer);
  }, [delay]);

  // 根据主题模式选择合适的链接颜色
  const linkColor = themeMode === 'dark' ? '#FFEFD5' : '#13479C';

  return (
    <AnimatePresence>
      {showQuestion && (
        <motion.span
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          onClick={() => onQuestionClick(question)}
          style={{ 
            color: linkColor,
            textDecoration: 'underline',
            cursor: 'pointer',
            fontWeight: '500',
          }}
        >
          <TypewriterText text={question} />
        </motion.span>
      )}
    </AnimatePresence>
  );
};

// 普通文本组件（用于 "or explore more..."）
const DelayedText = ({ text, delay = 0 }) => {
  const [showText, setShowText] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => {
      setShowText(true);
    }, delay);
    
    return () => clearTimeout(timer);
  }, [delay]);

  return (
    <AnimatePresence>
      {showText && (
        <motion.span
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          <TypewriterText text={text} />
        </motion.span>
      )}
    </AnimatePresence>
  );
};

// Fixed: 处理长链接和快捷问题的组件
const MessageContent = ({ text, isBot, showQuickQuestions, onQuestionClick, themeMode }) => {
  const [mainTextComplete, setMainTextComplete] = useState(false);
  
  // URL 正则表达式
  const urlRegex = /(https?:\/\/[^\s]+)/g;
  
  // 如果是机器人消息且包含快捷问题
  if (isBot && showQuickQuestions) {
    return (
      <span style={{ wordBreak: 'break-word', overflowWrap: 'break-word' }}>
        <TypewriterText text={text} onComplete={() => setMainTextComplete(true)} />
        {mainTextComplete && (
          <>
            <br />
            <DelayedText 
              text="Try:"
              delay={300}
            />
            <br />
            <span style={{ display: 'block' }}>
              <DelayedText text="- " delay={600} />
              <QuickQuestionLink 
                question="What are your hobbies?"
                onQuestionClick={onQuestionClick}
                delay={700}
                themeMode={themeMode}
              />
            </span>
            <span style={{ display: 'block' }}>
              <DelayedText text="- " delay={1100} />
              <QuickQuestionLink 
                question="What do you usually do at the weekend?"
                onQuestionClick={onQuestionClick}
                delay={1200}
                themeMode={themeMode}
              />
            </span>
          </>
        )}
      </span>
    );
  }
  
  if (isBot && urlRegex.test(text)) {
    // 将文本按链接分割
    const parts = text.split(urlRegex);
    
    return (
      <span style={{ wordBreak: 'break-all', overflowWrap: 'break-word' }}>
        {parts.map((part, index) => {
          if (urlRegex.test(part)) {
            return (
              <a 
                key={index} 
                href={part} 
                target="_blank" 
                rel="noopener noreferrer"
                style={{ 
                  color: 'inherit', 
                  textDecoration: 'underline',
                  wordBreak: 'break-all',
                  overflowWrap: 'break-word'
                }}
              >
                {part}
              </a>
            );
          }
          return <TypewriterText key={index} text={part} />;
        })}
      </span>
    );
  }
  
  if (isBot) {
    return <TypewriterText text={text} />;
  }
  
  return <span style={{ wordBreak: 'break-word', overflowWrap: 'break-word' }}>{text}</span>;
};

const QuickQuestions = ({ onQuestionClick, themeMode }) => {
  const questions = [
    "What are your hobbies?",
    "What do you usually do at the weekend?"
  ];

  return (
    <Box sx={{ mt: 2, display: 'flex', flexWrap: 'wrap', gap: 1 }}>
      {questions.map((question, index) => (
        <Chip
          key={index}
          label={question}
          onClick={() => onQuestionClick(question)}
          clickable
          variant="outlined"
          size="small"
          sx={{
            borderColor: themeMode === 'dark' ? 'grey.600' : 'primary.main',
            color: themeMode === 'dark' ? 'grey.300' : 'primary.main',
            fontSize: '0.75rem',
            '&:hover': {
              backgroundColor: themeMode === 'dark' ? 'grey.800' : 'primary.light',
              color: themeMode === 'dark' ? 'grey.100' : 'white',
            },
            transition: 'all 0.2s ease-in-out'
          }}
        />
      ))}
    </Box>
  );
};

const ChatbotCard = () => {
  const [messages, setMessages] = useState([
    { text: "I am Steven GPT! Feel free to ask me anything about myself.\n", sender: 'bot', showQuickQuestions: true }
  ]);
  const [inputText, setInputText] = useState('');
  const [loading, setLoading] = useState(false);
  const messageListRef = useRef(null);
  const isInitialRender = useRef(true);

  const { themeMode } = useContext(SettingsContext);
  const { userState } = useUser();

  const handleQuestionClick = (question) => {
    // 如果正在加载中，不允许发送新消息
    if (loading) return;
    
    // 直接发送消息
    handleSendMessageWithText(question);
  };

  const handleSendMessageWithText = async (text) => {
    if (text.trim() === '') return;

    if (text.length > 250) {
      const errorReply = {
        text: "Your query exceeds the maximum allowed length of 250 characters. Please shorten it.",
        sender: 'bot',
      };
      setMessages((prevMessages) => [...prevMessages, errorReply]);
      return;
    }

    const userMessage = { text: text, sender: 'user' };
    setMessages(prevMessages => [...prevMessages, userMessage]);

    setInputText('');
    setLoading(true);

    const loadingMessage = { text: "loading", sender: 'bot', loading: true };
    setMessages(prevMessages => [...prevMessages, loadingMessage]);

    try {
      const response = await ChatbotService.query(text, userState.username, userState.access);

      const botReply = {
        text: response.response || "I couldn't process your query. Please try again.",
        sender: 'bot'
      };

      setMessages(prevMessages => [
        ...prevMessages.slice(0, -1),
        botReply
      ]);
    } catch (error) {
      console.error('Error fetching chatbot response:', error);
      const errorReply = {
        text: "There was an error processing your query. Please try again later.",
        sender: 'bot'
      };
      setMessages(prevMessages => [
        ...prevMessages.slice(0, -1),
        errorReply
      ]);
    } finally {
      setLoading(false);
    }
  };

  const handleSendMessage = () => {
    handleSendMessageWithText(inputText);
  };

  useEffect(() => {
    if (isInitialRender.current) {
      isInitialRender.current = false;
      return;
    }

    if (messageListRef.current) {
      const scrollContainer = messageListRef.current;
      scrollContainer.scrollTop = scrollContainer.scrollHeight;
    }
  }, [messages]);

  return (
    <Box maxWidth="sm" 
        sx={{ 
        width: '100%',
        height: '600px',
        display: 'flex', 
        flexDirection: 'column' 
    }}>
      <Paper 
        elevation={3} 
        sx={{ 
          flex: 1, 
          display: 'flex', 
          flexDirection: 'column', 
          borderRadius: 2, 
          overflow: 'hidden',
          bgcolor: themeMode === 'dark' ? 'grey.900' : 'background.paper',
          color: themeMode === 'dark' ? 'grey.100' : 'text.primary'
        }}
      >
        
        <Box 
          sx={{ 
            bgcolor: themeMode === 'dark' ? '#1976D2' : 'primary.main', 
            color: themeMode === 'dark' ? 'grey.100' : 'white', 
            p: 2, 
            display: 'flex', 
            alignItems: 'center' 
          }}
        >
          <BotAvatar sx={{ mr: 2 }} />
          <Typography variant="h6">Steven GPT</Typography>
        </Box>

        {/* Fixed: Removed component="main" to avoid ARIA role conflict */}
        <Box
          role="log"
          aria-label="Chat messages"
          aria-live="polite"
          ref={messageListRef}
          sx={{ 
            flex: 1, 
            overflowY: 'auto', 
            p: 2,
            bgcolor: themeMode === 'dark' ? 'grey.900' : 'background.paper'
          }}
        >
          <AnimatePresence>
            {messages.map((msg, index) => (
              <motion.div
                key={index}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.3 }}
              >
                <Box
                  sx={{ 
                    display: 'flex',
                    justifyContent: msg.sender === 'user' ? 'flex-end' : 'flex-start',
                    alignItems: 'flex-start',
                    mb: 2
                  }}
                >
                  {/* 机器人头像 - 只在机器人消息时显示 */}
                  {msg.sender === 'bot' && (
                    <Avatar
                      sx={{ 
                        bgcolor: themeMode === 'dark' ? '#1E88E5' : '#1976d2',
                        width: 32,
                        height: 32,
                        mr: 1,
                        mt: 1.5
                      }}
                    >
                      <BotAvatar sx={{ fontSize: 18, color: "white" }} />
                    </Avatar>
                  )}

                  {msg.loading ? (
                      <CircularProgress size={20} sx={{ marginLeft: 2, marginTop: 2.2 }}/>
                  ) : (
                    <Box
                      role={msg.sender === 'bot' ? 'status' : undefined}
                      aria-label={msg.sender === 'bot' ? 'Bot message' : 'Your message'}
                      sx={{
                        maxWidth: msg.sender === 'user' ? '65%' : '65%', // 机器人消息稍微窄一点给头像留空间
                        bgcolor: msg.sender === 'user' 
                          ? (themeMode === 'dark' ? 'grey.800' : 'grey.200') 
                          : (themeMode === 'dark' ? '#1E88E5' : '#BBDEFB'),
                        color: themeMode === 'dark' ? 'white' : 'black',
                        p: 1.5,
                        borderRadius: 2,
                        marginTop: 1,
                        marginLeft: msg.sender === 'user' ? 1.5 : 0, // 用户消息保持左边距
                        display: 'flex',
                        alignItems: 'flex-start'
                      }}
                    >
                      <Typography
                        variant="body2"
                        sx={{
                          fontSize: '1rem',
                          whiteSpace: 'pre-wrap',
                          wordBreak: 'break-word',
                          overflowWrap: 'break-word'
                        }}
                      >
                        <MessageContent 
                          text={msg.text} 
                          isBot={msg.sender === 'bot'}
                          showQuickQuestions={msg.showQuickQuestions}
                          onQuestionClick={handleQuestionClick}
                          themeMode={themeMode}
                        />
                      </Typography>
                    </Box>
                  )}
                </Box>
              </motion.div>
            ))}
          </AnimatePresence>
        </Box>

        <Box 
          component="form"
          onSubmit={(e) => {
            e.preventDefault();
            handleSendMessage();
          }}
          sx={{ 
            p: 2, 
            borderTop: '1px solid', 
            borderColor: themeMode === 'dark' ? 'grey.700' : 'divider', 
            display: 'flex',
            bgcolor: themeMode === 'dark' ? 'grey.900' : 'background.paper'
          }}
        >
          <TextField
            fullWidth
            variant="outlined"
            placeholder="Enter another query..."
            value={inputText}
            onChange={(e) => setInputText(e.target.value)}
            onKeyPress={(e) => e.key === 'Enter' && handleSendMessage()}
            inputProps={{
              'aria-label': 'Type your message',
              maxLength: 250
            }}
            sx={{ 
              mr: 2,
              bgcolor: themeMode === 'dark' ? 'grey.800' : 'white',
              input: { color: themeMode === 'dark' ? 'grey.100' : 'black' }
            }}
            disabled={loading}
          />
          <IconButton 
            type="submit"
            color="primary" 
            onClick={handleSendMessage}
            sx={{ borderRadius: 2 }}
            disabled={loading}
            aria-label="Send message"
          >
            <SendIcon />
          </IconButton>
        </Box>
      </Paper>
    </Box>
  );
};

export default ChatbotCard;