import { useState, useContext } from "react";
import {
  Card,
  CardContent,
  CardMedia,
  Typography,
  Grid,
  IconButton,
  Box,
  Link,
} from "@mui/material";
import { SettingsContext } from "../../context/SettingsContext";
import EmailIcon from "@mui/icons-material/Email";
import InstagramIcon from "@mui/icons-material/Instagram";
import SchoolIcon from "@mui/icons-material/School";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import GitHubIcon from "@mui/icons-material/GitHub";

// 导入两张图片，确保它们都被预加载
import stevenPhoto from "../../assets/img/steven.jpeg";
// import stevenCartoon from "../../assets/img/steven_cartoon_2.jpeg";
import stevenCartoon from "../../assets/img/steven_cartoon.jpeg";

const ProfileCard = () => {
  // 使用导入的图片作为初始状态
  const [avatar, setAvatar] = useState(stevenPhoto);
  const { language, themeMode } = useContext(SettingsContext); // 从 Context 中获取语言状态

  // 切换图片的处理函数 - 使用预加载的图片变量
  const handleAvatarClick = () => {
    setAvatar(prevAvatar => prevAvatar === stevenPhoto ? stevenCartoon : stevenPhoto);
  };

  // 根据语言状态动态设置文本内容
  const name = "Xi Luo";
  const title = language === "en" ? "Junior Machine Learning Engineer" : "初级机器学习工程师";

  // 根据主题模式设置图标颜色
  const primaryIconColor = themeMode === "dark" ? "#64B5F6" : "#0066CC";

  return (
    <Card
      sx={{
        width: 320,
        padding: 2,
        textAlign: "center",
        boxShadow: "none !important", // 强制移除阴影
        background: "transparent !important", // 添加 background 属性
      }}
    >
      {/* 头像 */}
      <CardMedia
        component="img"
        alt={name}
        src={avatar}
        onClick={handleAvatarClick} // 添加点击事件处理函数
        sx={{
          width: 200,
          height: 200,
          margin: "0 auto",
          borderRadius: "50%", // 圆形效果
          cursor: "pointer", // 添加鼠标指针样式
        }}
      />
      {/* 内容 */}
      <CardContent>
        <Typography variant="h6" component="div">
          {name}
        </Typography>
        <Typography variant="subtitle1" color="text.secondary">
          {title}
        </Typography>
        <Link
          href="https://addaxis.ai"
          target="_blank" // 在新标签页中打开
          rel="noopener noreferrer" // 安全属性
          color="inherit" // 继承父组件颜色
          underline="hover" // 设置下划线样式
          sx={{ mt: 1, display: "inline-block" }}
        >
          @addaxis.ai
        </Link>
      </CardContent>

      {/* 按钮区域 */}
      <Box>
        <Grid container justifyContent="center" spacing={2}>
          <Grid item>
            <IconButton 
              href="mailto:steven@xiluo.net" 
              aria-label="email"
              sx={{ color: primaryIconColor }}
            >
              <EmailIcon />
            </IconButton>
          </Grid>
          <Grid item>
            <IconButton href="https://www.instagram.com/stevluo32/" color="secondary" aria-label="instagram">
              <InstagramIcon />
            </IconButton>
          </Grid>
          <Grid item>
            <IconButton href="https://github.com/luoxisteven" color="default" aria-label="github">
              <GitHubIcon />
            </IconButton>
          </Grid>
          <Grid item>
            <IconButton 
              href="https://scholar.google.com.au/citations?user=YtYvNGMAAAAJ" 
              aria-label="google scholar"
              sx={{ color: primaryIconColor }}
            >
              <SchoolIcon />
            </IconButton>
          </Grid>
          <Grid item>
            <IconButton 
              href="https://www.linkedin.com/in/steven-luo-1a0ab8272/" 
              aria-label="linkedin"
              sx={{ color: primaryIconColor }}
            >
              <LinkedInIcon />
            </IconButton>
          </Grid>
        </Grid>
      </Box>
    </Card>
  );
};

export default ProfileCard;